body {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.active {
    background: #1d2a4a !important;
}

.chats{
    padding-top: 1rem;
    /* background-color: #1d2a4a; */
    color: aliceblue;

    
}
.framewrapper{
    /* position: absolute;
    z-index: 9999; */
    
      /* background-color: #1d2a4a; */
    overflow: hidden;
}
.framewrapper iframe{
    /* position: relative;
    z-index: 6; */
    height: 75px;
    /* overflow: hidden !important; */
    scrollbar-width: 0;

}
.framewrapper button{
    background-color: #2f2d52;
    border: none;
    font-family: 'Roboto', sans-serif;
    color: whitesmoke;
    border-radius: 5px;
    cursor: pointer;  
    

}
.framewrapper button:hover{
    background-color: white;
    color: #2f2d52;
    transition: all ease 0.4s;
}