body.active-modal {
    overflow-y: hidden;
}


.modal, .overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;    
    display: flex;
    justify-content: center;
    align-items: center;
   

}

.overlay {
    background: rgba(49,49,49,0.8);
}
.modal-content {
    position: absolute;
    /* top: 40%;
    left: 50%;
    transform: translate(-50%, -50%); */
    line-height: 1.4;
    background: #f1f1f1;
    padding: 14px 28px;
    border-radius: 3px;
    width: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
   
}
img{
    cursor: pointer;
}
.modal-content img{
    width: 100%;
}
.close-modal {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 5px 7px;
    cursor: pointer;
    border: none;
    background-color: #5d5b8d;
    transition: ease 0.4s;
}
.close-modal:hover {
    background-color: white;
}